.menu {
  box-sizing: border-box;
  height: 100%;
  background-color: $red-color;
  text-transform: uppercase;
  color: $fff;

  &__top {
    margin-bottom: 40px;
  }

  &__nav {
    margin-bottom: 32px;
  }

  &__content {
    @include constent-padding-in-line;
  }

  &__phone-text {
    margin-bottom: 4px;
    display: block;

    &:hover {
      color: transparent;
      background-image: linear-gradient(
        90deg,
        rgba(73, 62, 80, 1) 0%,
        rgba(237, 200, 200, 1) 50%,
        rgba(252, 204, 69, 1) 100%
      );
      -webkit-background-clip: text;
      background-clip: text;
      background-size: 300px 300px;
      background-position: 0 0;
      animation: bp 2s infinite linear;
    }

    @keyframes bp {
      from {
        background-position-x: 0;
      }
      to {
        background-position-x: 300px;
      }
    }
  }

  &__phone-number {
    margin-bottom: 8px;
    display: block;

    &:hover {
      color: transparent;
      background-image: linear-gradient(
        90deg,
        rgba(73, 62, 80, 1) 0%,
        rgba(237, 200, 200, 1) 50%,
        rgba(252, 204, 69, 1) 100%
      );
      -webkit-background-clip: text;
      background-clip: text;
      background-size: 300px 300px;
      background-position: 0 0;
      animation: bp 2s infinite linear;
    }

    @keyframes bp {
      from {
        background-position-x: 0;
      }
      to {
        background-position-x: 300px;
      }
    }
  }

  &__call-to-order {
    width: fit-content;
    padding-bottom: 7px;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;

    &::after {
      content: "";
      bottom: 8px;
      display: block;
      height: 1px;
      width: 100%;
      background-color: $fff;

      transition: transform 0.3s;
      transform: scale(0);
      transform-origin: left;
    }

    &:hover::after {
      transform: scale(1);
    }
  }
}

.phone-number-cloisters {
  margin-bottom: 16px;
}
