.top-bar {
  padding-block: 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include tablet {
    padding-block: 64px;
  }

  &__logo {
    display: flex;
    transition: filter 0.3s;

    @include hover(transform, scale(1.2));
  }

  &__link {
    &:hover {
      filter: invert(60%) sepia(80%) saturate(3338%) hue-rotate(334deg)
        brightness(88%) contrast(102%);
    }
  }

  &__icon {
    display: flex;
    gap: 27px;
    position: relative;

    @include tablet {
      gap: 59px;
    }
  }
}
