* {
  margin: 0;
  padding: 0;
  border: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

html {
  scroll-behavior: smooth;
}

main {
  display: block;
}

h1,
h2,
h3 {
  font-size: 1em;
  margin: 0;
}

input,
label,
textarea {
  display: block;
  font-weight: inherit;
  font-size: inherit;
  color: inherit;
  font-family: inherit;
  outline: none;
}

button {
  cursor: pointer;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  background-color: inherit;
}

body.hide {
  overflow: hidden;
}

svg path {
  fill: inherit;
  stroke: inherit;
  stroke-width: inherit;
}
